.setting-content {
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  position: relative;
}
.setting-content .prop-item {
  cursor: pointer;
  font-size: 14px;
  margin-bottom: 5px;
}
.setting-content .prop-item:hover {
  color: #2db7f5;
  border-color: #2db7f5;
}
.setting-content .prop-item .anticon {
  margin-left: 6px;
  font-size: 14px;
}
.setting-content .site-tag-plus {
  cursor: pointer;
  background: #fff;
  border-style: dashed;
  font-size: 14px;
  margin-bottom: 5px;
}
.setting-content .site-tag-plus:hover {
  color: #2db7f5;
  border-color: #2db7f5;
}
.setting-content .in-title {
  margin-bottom: 20px;
}
.setting-content .in-title .ant-descriptions-title {
  margin-bottom: 10px;
  text-align: center;
}
.setting-content .title {
  margin-top: 20px;
  margin-bottom: 10px;
  color: rgba(0, 0, 0, 0.85);
  font-weight: bold;
  font-size: 16px;
  line-height: 1.5715;
  text-align: center;
}
